.header {
    height: 230px;
    padding-top: 46px;
    background-color: $logo-color;;

}
.header--home {
      @media screen and (max-width: $max-width) {
          background: url('../images/header-img/header-home-mob-1x.jpg');
          background-repeat: no-repeat;
          background-position: center;
          background-size: cover;
      }

      @media (min-device-pixel-ratio: 2) and (max-width: $max-width),
      (min-resolution: 192dpi) and (max-width: $max-width),
      (min-resolution: 2dppx) and (max-width: $max-width) {
          background: url('../images/header-img/header-home-mob-2x.jpg');
          background-repeat: no-repeat;
          background-position: center;
          background-size: cover;
      }

      @media screen and (min-width: $tab) {
          background: url('../images/header-img/header-home-tab-1x.jpg');
          background-repeat: no-repeat;
          background-position: center;
          background-size: cover;
      }

      @media (min-device-pixel-ratio: 2) and (min-width: $tab),
      (min-resolution: 192dpi) and (min-width: $tab),
      (min-resolution: 2dppx) and (min-width: $tab) {
          background: url('../images/header-img/header-home-tab-2x.jpg');
          background-repeat: no-repeat;
          background-position: center;
          background-size: cover;
      }

      @media screen and (min-width: $desk) {
          background: url('../images/header-img/header-home-desk-1x.jpg');
          background-repeat: no-repeat;
          background-position: center;
          background-size: cover;
      }

      @media (min-device-pixel-ratio: 2) and (min-width: $desk),
      (min-resolution: 192dpi) and (min-width: $desk),
      (min-resolution: 2dppx) and (min-width: $desk) {
          background: url('../images/header-img/header-home-desk-2x.jpg');
          background-repeat: no-repeat;
          background-position: center;
          background-size: cover;
      }
  }

  .header--library {
        @media screen and (min-width: $mob) {
                background: url('../images/header-img/header-lib-mob-1x.jpg');
