// ===
.header__container {
    max-width: 100%;
    margin-left: auto;
    margin-right: auto;
    padding-left: 20px;
    padding-right: 20px;

    @media screen and (min-width: $mob) {
        width: $mob;
    }

    @media screen and (min-width: $tab) {
        width: $tab;
        padding-left: 75px;
        padding-right: 75px;
    }

    @media screen and (min-width: $desk) {
        width: $desk;
        padding-left: 70px;
        padding-right: 70px;
    }
}

.header__nav {
    display: flex;
    justify-content: space-between;
    align-items: center;

    &-list {
        display: flex;
        list-style: none;
    }

    &-item {
        position: relative;

        &--active::after {
            content: '';
            position: absolute;
            left: 0;
            bottom: 0px;
            width: 100%;
            height: 3px;
            background-color: $accent-color;
        }
    }

    &-item:not(:last-child) {
        margin-right: 39px;
    }
}

.header__logo {
    align-items: center;
    display: flex;

    &-icon {
        width: 24px;
        height: 24px;
        margin-right: 12px;
        fill: $logo-color;

        @media screen and (min-width: $tab) {
            margin-right: 10px;
        }
    }

    &:hover> {
        svg {
            stroke: $logo-color;
        }

        span {
            color: $logo-color;
        }
    }

    &-name {
        // transition: color $animation-duration $timing-function;

        @media screen and (max-width: 767px) {
            display: none;
        }

        font-size: 30px;
        color: $secondary-text-color;
        // transition: color $animation-duration $timing-function;
    }
}

.header__nav-btn {
    background-color: transparent;
    border: none;
    margin: 0px;
    padding-right: 0px;
    padding-left: 0px;
    padding-top: 7px;
    padding-bottom: 5px;
    line-height: 1.17;
    text-transform: uppercase;
    color: $secondary-text-color;
    cursor: pointer;
    
    transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.header__nav-btn:hover,
.header__nav-btn:focus {
    color: $accent-color;
    transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);
}
.header__filmsearch {
    position: relative;
    margin-top: 37px;
}
.header__form {
    display: flex;
    justify-content: center;
    &-input {
            background-color: transparent;
            border-top: none;
            border-right: none;
            border-left: none;
            border-bottom: 0.5px solid $secondary-text-color;
            padding-top: 0px;
            padding-left: 0px;
            padding-right: 20px;
            padding-bottom: 4px;
            color: white;
            outline: none;
            
    
            &-btn {
                border: none;
                background-color: inherit;
                transform: translateX(-100%);
                padding: 0px;
            }
    
            @media screen and (min-width: $mob) and (max-width: $max-width) {
                width: 278px;
            }
    
            width: 336px;
    
            
        }
        &-search-icon {
            stroke: $secondary-text-color;
            
        }
}